var BlockMenu = require("components/block_menu");
var BlockTypes = require("stores/block_type_store");
var EditorBlock = require("components/editor_block");
var Fullscreen = require("./fullscreen");
var React = require("react");
var fullscreen = require("vendor/requestFullscreen");

var App = React.createClass({
  displayName: "App",

  childContextTypes: {
    types: React.PropTypes.array.isRequired
  },

  propTypes: {
    block: React.PropTypes.object.isRequired
  },

  getChildContext: function getChildContext() {
    return {
      types: this.props.types
    };
  },

  getDefaultProps: function getDefaultProps() {
    return {
      onFullscreen: fullscreen,
      types: BlockTypes.keys()
    };
  },

  goFullscreen: function goFullscreen() {
    this.props.onFullscreen(this.getDOMNode());
  },

  render: function render() {
    var block = this.props.block;

    return React.createElement(
      "div",
      { className: "colonel" },
      React.createElement(Fullscreen, { ref: "fullscreen", onClick: this.goFullscreen }),
      React.createElement(BlockMenu, { block: block }),
      React.createElement(EditorBlock, { block: block })
    );
  }

});

module.exports = App;


